import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import Link from "next/link";

export default function UserPage() {
  return (
    <div className="flex flex-col gap-4 text-white px-6 py-4">
      {/* 用户名输入框 */}
      <Input
        type="text"
        placeholder="用户名或电子邮件"
        className="bg-transparent border-white/30 text-white placeholder:text-white/70 h-12 rounded-lg"
      />

      {/* 密码输入框 */}
      <Input
        type="password"
        placeholder="密码"
        className="bg-transparent border-white/30 text-white placeholder:text-white/70 h-12 rounded-lg"
      />

      {/* 忘记密码和注册新用户链接 */}
      <div className="flex justify-between items-center text-sm">
        <Link href="/user/refind" className="text-white/80 hover:text-white">
          忘记密码？
        </Link>
      </div>

      {/* 同意条款复选框 */}
      <div className="flex items-center space-x-2">
        <Checkbox id="terms" className="border-white/30 data-[state=checked]:bg-white data-[state=checked]:text-black" />
        <Label htmlFor="terms" className="text-white/80 text-sm cursor-pointer">
          我已经阅读并同意《旅游·中国用户协议》
        </Label>
      </div>

      {/* 登录按钮 */}
      <Button
        className="bg-[#B85450] hover:bg-[#A04A46] text-white h-12 rounded-lg font-medium text-base mt-4"
      >
        登录
      </Button>
    </div>
  );
}